//-------------------------------------------------
// Colors

// use with color mixin. 400 is default.

// EXAMPLE:
// background: color('grey', 100);
// color: color('grey');

$colors: (
	'black': (
		1: #333333
	),
	'grey': (
		1: #626261 ,
		2: #bfbfbf
	),
	'cream': (
		1: #F8F2E5
	),

	'red': (
		1: #eb5a2d,	//hover
		2: #d1532c //brend hover
	),

	'blue': (
		//1: #1186c0 // .t_color--blue-1
	),

	'green': (
		//1: #1ac604 // .t_color--green-1
	)
);


